import { StaticImage } from "gatsby-plugin-image";
import React from "react";
import { Row, Col, Card } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import { Link } from "gatsby";
import binance from "../../styles/assets/images/centralize-exchange/binance.webp";

const CentralizedCryptoCoins = (props) => {
  const { db } = props;
  return (
    <div className="centralize_coin_wrapper">
      <Row className="rg_30 mx-auto mt_50 pt_30">
        <Col md={12} className="text-center">
          <h2>Popular Centralized Crypto Exchange We Offer</h2>
        </Col>
        <Col md={10} className="text-center mx-auto">
          <p>
            There are many sought-after clone scripts in the market for
            centralized crypto exchanges. Let's explore some of the more popular
            options.
          </p>
        </Col>
      </Row>
      <Row className="rg_30">
        <Col lg={4} sm={6} xs={12} className="col_flex">
          <Card>
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/binance.webp"
              formats={["auto", "webp"]}
            />
            <span>Binance</span>
          </Card>
        </Col>
        <Col lg={4} sm={6} xs={12} className="col_flex">
          <Card>
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/coinbase.webp"
              formats={["auto", "webp"]}
            />
            <span>Coinbase</span>
          </Card>
        </Col>
        <Col lg={4} sm={6} xs={12} className="col_flex">
          <Card>
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/okx.webp"
              formats={["auto", "webp"]}
            />
            <span>OKX</span>
          </Card>
        </Col>
        <Col lg={4} sm={6} xs={12} className="col_flex">
          <Card>
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/kucoin.webp"
              formats={["auto", "webp"]}
            />
            <span>Kucoin</span>
          </Card>
        </Col>
        <Col lg={4} sm={6} xs={12} className="col_flex">
          <Card>
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/huobi.webp"
              formats={["auto", "webp"]}
            />
            <span>Huobi</span>
          </Card>
        </Col>
        <Col lg={4} sm={6} xs={12} className="col_flex">
          <Card>
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/bitfinex.webp"
              formats={["auto", "webp"]}
            />
            <span>Bitfinex</span>
          </Card>
        </Col>
      </Row>
      <Row>
        <div className="crypto_btns_grp mt_30 justify-content-center">
          <Link className="prime_submit_btn" to="/contact-us">
            View Live Demo
          </Link>
          <button className="second_consult_btn" type="button">
            <TalkToUsNew label="Free Consult" db={db} />
          </button>
        </div>
      </Row>
    </div>
  );
};

export default CentralizedCryptoCoins;
