import React, { Fragment } from "react";
import { Col, Container, Row } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import ConnectButton from "../../components/common/connect-button";
import { Link } from "gatsby";
import Faq from "../../components/common/faq";
import CentralizedProcess from "./centralized-process";
import { StaticImage } from "gatsby-plugin-image";
import CentralizedCryptoProcess from "./centralized-tab";
import CtaBtnThree from "../../components/common/contactbutthonthree";
import CentralizedPlatform from "./centralized-platform";
import CentralizedCryptoCoins from "./centralized-crypto";

const CentralizedCryptoScreen = (props) => {
  const { db } = props;

  const CentralizedFaq = [
    {
      question: "What is a centralized crypto exchange?",
      answer:
        "Centralized crypto exchange: Platform for trading, managed by central authority.",
    },
    {
      question: "What are popular centralized Exchanges?",
      answer: "Binance, Coinbase, Kraken, Kucoin, Bitfinex and so on.",
    },
    {
      question: "Estimated cost to build a centralized crypto exchange?",
      answer: "It depends on features and customization.",
    },
    {
      question: "Why Centralized crypto exchange?",
      answer:
        "Efficient trading, liquidity, user-friendly experience – benefits of centralized crypto exchanges.",
    },
  ];
  return (
    <Fragment>
      <div className="centralized_bg">
        <Container>
          <Row className="rg_50">
            <Col xl={7} lg={12}>
              <div className="centralized_hero_sec">
                <h1>
                  Centralized Exchange Development{" "}
                  <span className="d-xxl-block">Company</span>
                </h1>
                <p className="text-white">
                  Get our centralized exchange development firm to help you
                  build a feature-rich cryptocurrency exchange platform. Deliver
                  your users with a top-notch solution to make cryptocurrency
                  trading as simple as possible!
                </p>
                <div className="decenter_hero_sec_btn">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
            <Col xl={5} lg={12} className="col_flex">
              <StaticImage
                className="centralized_hero_img_wrapper"
                src="../../styles/assets/images/centralize-exchange/hero_image.webp"
                formats={["auto", "webp"]}
              />
            </Col>
          </Row>
        </Container>
      </div>

      <Container>
        <Row className="mt_50 rg_50">
          <Col xl={5} className="col_flex">
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/centralized_dev.webp"
              formats={["auto", "webp"]}
            />
          </Col>
          <Col xl={7} className="mx-auto text-start">
            <div className="centralized_dev_rhs mw_lg">
              <h2>Centralized Cryptocurrency Exchange Development Company</h2>

              <p>
                Cryptocurrencies are in full swing as more people use these
                computer-based digital currencies. Centralized crypto exchanges
                are key to helping millions of users enter this new money world.
                Our company helps create these platforms, making it easy for
                users to switch to Web3 with interfaces similar to what they
                already know from Web2.
              </p>

              <p>
                Our experts can bring your crypto exchange ideas to life using
                advanced technology. Choose between our complete exchange
                development services or ready-made white-label exchange software
                based on your business needs. Contact our experts now to know
                more.
              </p>

              <ConnectButton justifyContentPosition="connect_start" />
            </div>
          </Col>
        </Row>
        <CentralizedCryptoProcess />
      </Container>

      <div className="centralized_cta_wrapper">
        <CtaBtnThree
          db={db}
          dynamicButtonText="Elevate Your Exchange Now! <span class='d-xl-block'>Explore Seamless Centralized Solutions with Us.<span>"
        />
      </div>

      <Container>
        <Row className="mt_50  rg_30 mb_50 pb_50">
          <Col xl={8} className="mx-auto text-center mt_50">
            <h2>
              Popular Crypto Trading Types in Centralized Exchanges (CEXs)
            </h2>
          </Col>
          <Col xl={10} className="mx-auto text-center">
            <p>
              To further enhance trader functionality, we help you launch a
              centralized cryptocurrency exchange application that includes
              multiple trading types. We can design platforms tailored to your
              target market with all the necessary features to guarantee simple
              user experiences.
            </p>
          </Col>
          <Row className="centralize_trade_card_wrappper rg_50">
            <Col md={6}>
              <div className="centralize_trade_type_box">
                <span className="trade_type_title">Spot Trading</span>
                <p>
                  Spot Trading executes swift exchanges of financial
                  instruments, like currencies or commodities, at the prevailing
                  market price. Known for instant settlement, it's a go-to
                  method for rapid, short-term transactions in the financial
                  landscape.
                </p>
              </div>
            </Col>
            <Col md={6}>
              <div className="centralize_trade_type_box">
                <span className="trade_type_title">Future Trading</span>
                <p>
                  Future Trading, on the other hand, involves agreements to buy
                  or sell assets at a predetermined price on a specified future
                  date. This type of trading allows investors to hedge against
                  price fluctuations and can be used for speculation on the
                  future direction of prices.
                </p>
              </div>
            </Col>
          </Row>
          <Col md={12} className="mt_30">
            <div className="crypto_btns_grp justify-content-center ">
              <Link className="prime_submit_btn" to="/contact-us">
                View Live Demo
              </Link>
              <button className="second_consult_btn" type="button">
                <TalkToUsNew label="Free Consult" db={db} />
              </button>
            </div>
          </Col>
        </Row>
        <Row className="rg_30 mt_50 pt_50 mb_50">
          <Col xl={12} className="mx-auto text-center">
            <h2>Key Features Of Centralized Exchange Platform</h2>
          </Col>
          <Col xl={10} className="mx-auto text-center">
            <p>
              As a leading centralized crypto exchange development company, we
              assure complete coverage and distinctive business-oriented
              features for our CEX Exchange solutions.
            </p>
          </Col>
        </Row>
        <CentralizedPlatform />
        <CentralizedCryptoCoins db={db} />
      </Container>
      <div className="centralized_benefit_wrapper">
        <Container>
          <Row className="rg_30">
            <Col xl={6} className="col_flex">
              <StaticImage
                src="../../styles/assets/images/centralize-exchange/centralized_benefits.webp"
                formats={["auto", "webp"]}
              />
            </Col>
            <Col xl={6} className="col_flex p-xl-0">
              <Row className="centralized_benefit_rhs rg_50 mw_lg p-xl-0">
                <Col md={12}>
                  <h2 className="text-white">
                    Business Benefits of our Centralized Exchange Development
                    Services
                  </h2>
                </Col>
                <Col md={12}>
                  <p className="text-white">
                    Unlock unparalleled business advantages with our Centralized
                    Exchange Development Services. From enhanced security to
                    seamless transactions, our solutions pave the way for
                    elevated user experiences and robust financial ecosystems,
                    ensuring your business stays at the forefront of innovation
                    in the digital exchange realm.
                  </p>
                </Col>
                <Col md={6} className="p-xl-0">
                  <div className="centralized_ul">
                    <div className="centralized_li">
                      <li>Customization</li>
                    </div>
                    <div className="centralized_li">
                      <li>Security Measures</li>
                    </div>
                    <div className="centralized_li">
                      <li>User-Friendly Interface</li>
                    </div>
                    <div className="centralized_li">
                      <li>Advanced Trading Features</li>
                    </div>
                  </div>
                </Col>
                <Col md={6} className="p-xl-0">
                  <div className="centralized_ul">
                    <div className="centralized_li">
                      <li>Compliance and Regulations</li>
                    </div>
                    <div className="centralized_li">
                      <li>Technical Support</li>
                    </div>
                    <div className="centralized_li">
                      <li>Liquidity Enhancement</li>
                    </div>
                    <div className="centralized_li">
                      <li>Cose-Effective Solutions</li>
                    </div>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
      <Container>
        <CentralizedProcess />
        <Row className="mt_50 pt_30 rg_50">
          <Col md={8} className="mx-auto text-center">
            <h2>
              Why Choose Justtry As Your Centralized Exchange Development
              Company?
            </h2>
          </Col>
          <Col
            lg={6}
            className="d-flex justify-content-center align-items-center d-lg-block"
          >
            <StaticImage
              src="../../styles/assets/images/centralize-exchange/centralized_choose.webp"
              formats={["auto", "webp"]}
            />
          </Col>
          <Col lg={6}>
            <p>
              At Justtry Technologies, we excel as a centralized exchange
              development firm due to our technological prowess. Our seasoned
              blockchain developers deliver end-to-end solutions encompassing
              order-matching engines, wallet integration, robust security, and
              scalable architecture. The outcome is a tailored, secure, and
              high-performance exchange designed precisely to meet your
              distinctive needs. Backed by a proven track record and unwavering
              commitment to excellence, Justtry Technologies is your trusted
              partner in the realm of centralized crypto exchange development.
            </p>
            <div className="crypto_btns_grp mt_30 justify-content-start">
              <Link className="prime_submit_btn" to="/contact-us">
                View Live Demo
              </Link>
              <button className="second_consult_btn" type="button">
                <TalkToUsNew label="Free Consult" db={db} />
              </button>
            </div>
          </Col>
        </Row>
      </Container>
      <Faq faqData={CentralizedFaq} />
    </Fragment>
  );
};

export default CentralizedCryptoScreen;
