import React from "react";
import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { Link } from "gatsby";

const CentralizedCryptoProcess = () => {
  return (
    <>
      <Container className="mt_50">
        <Row className="dao_rows text-center">
          <h2>How does a Centralized Exchange Work?</h2>
        </Row>
        <Row className="dao_dev_tabs mt_50">
          <Tab.Container id="left-tabs-example" defaultActiveKey="first">
            <Row>
              <Col md={4} sm={12}>
                <Nav
                  variant="pills"
                  className="flex-row flex-md-column flex-nowrap justify-content-md-center justify-content-start"
                >
                  <Nav.Item>
                    <Nav.Link eventKey="first">
                      <span>1.</span>User Registration
                    </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="second">
                      <span>2.</span>Deposit Funds
                    </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="third">
                      <span>3.</span>Order Placement and Matching{" "}
                    </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="fourth">
                      <span>4.</span>Execution and Confirmation
                    </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="fifth">
                      <span>5.</span>Withdrawal of Funds
                    </Nav.Link>
                  </Nav.Item>
                </Nav>
              </Col>
              <Col md={8} sm={12} className="border_tab">
                <Tab.Content>
                  <Tab.Pane eventKey="first">
                    <p>
                      Users create accounts on the centralized exchange,
                      providing necessary details and completing the
                      registration process.
                    </p>

                    <Link className="dao_tab_btn" to="/contact-us">
                      View Live Demo
                    </Link>
                  </Tab.Pane>
                  <Tab.Pane eventKey="second">
                    <p>
                      Once registered, users deposit cryptocurrencies or fiat
                      currency into their exchange wallets.
                    </p>
                    <Link className="dao_tab_btn" to="/contact-us">
                      View Live Demo
                    </Link>
                  </Tab.Pane>
                  <Tab.Pane eventKey="third">
                    <p>
                      Users place buy or sell orders, specifying prices and
                      quantities. The exchange's matching engine pairs
                      compatible orders, executing trades.
                    </p>
                    <Link className="dao_tab_btn" to="/contact-us">
                      View Live Demo
                    </Link>
                  </Tab.Pane>
                  <Tab.Pane eventKey="fourth">
                    <p>
                      When orders match, the exchange executes the trade,
                      transferring assets between buyer and seller accounts.
                      Both parties receive confirmations.
                    </p>

                    <Link className="dao_tab_btn" to="/contact-us">
                      View Live Demo
                    </Link>
                  </Tab.Pane>
                  <Tab.Pane eventKey="fifth">
                    <p>
                      Users can withdraw their funds, converting them back to
                      cryptocurrencies or fiat, and transfer them to personal
                      wallets or bank accounts.
                    </p>
                    <Link className="dao_tab_btn" to="/contact-us">
                      View Live Demo
                    </Link>
                  </Tab.Pane>
                </Tab.Content>
              </Col>
            </Row>
          </Tab.Container>
        </Row>
      </Container>
    </>
  );
};

export default CentralizedCryptoProcess;
